}

/* pc, mobile */
#document {
  overflow: hidden;
}

.hidden-p {
  display: none !important;
}

@media (max-width: 751px) {
  .hidden-p {
    display: block !important;
  }
}

.hidden-m {
  display: block !important;
}

@media (max-width: 751px) {
