@keyframes frameCrossRow {
  0% {
    transform: translateX(-100%);
    width: 0%;
    left: 0%;
    opacity: 1;
    height: 1px;
  }
  20% {
    transform: translateX(0%);
    width: 100%;
    left: 0%;
    opacity: 1;
    height: 1px;
  }
  30% {
    transform: translateX(calc(var(--user-cross-effect-size)/-2));
    width: var(--user-cross-effect-size);
    left: 50%;
    opacity: 1;
    height: 1px;
  }
  40% {
    transform: translateX(calc(var(--user-cross-effect-size)/-2));
    width: var(--user-cross-effect-size);
    left: 50%;
    opacity: 1;
    height: 2px;
    background: white;
  }
  45% {
    transform: translateX(0%);
    width: 0px;
    left: 50%;
    opacity: 0;
    height: 2px;
    background: white;
  }
  60% {
    transform: translateX(0%);
    width: 0px;
    left: 50%;
    opacity: 0;
    height: 2px;
    background: rgba(255, 255, 255, 0.5);
  }
  70% {
    transform: translateX(0%);
    width: 0px;
    left: 50%;
    opacity: 0;
    height: 2px;
    background: rgba(255, 255, 255, 0.5);
  }
  100% {
    transform: translateX(0%);
    width: 0px;
    left: 50%;
    opacity: 0;
    height: 2px;
    background: rgba(255, 255, 255, 0.5);
  }
}

