.skip-navi {
  position: absolute;
  width: 100%;
  font-size: 0;
}

.skip-navi__linker {
  position: absolute;
  top: -999px;
  left: 0;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
  text-align: center;
  background: #000;
}

.skip-navi__linker:active, .skip-navi__linker:focus {
  overflow: hidden;
  top: 0;
  text-decoration: none;
  z-index: 101;
}

.c-gnb {
  z-index: 5;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 0 80px;
  background-color: transparent;
}

@media (max-width: 751px) {
  .c-gnb {
    padding: 0 20px;
  }
}

.c-gnb__logo {
  z-index: 6;
  position: relative;
  display: block;
  top: -100px;
  width: 208px;
  height: 20px;
  background: url(../img/common/pivot_logo_1_k.png) no-repeat center center;
  background-size: 100%;
  transition: background-image .5s ease;
}

@media (max-width: 751px) {
  .c-gnb__logo {
    width: 83.5px;
    height: 32.5px;
    background: url(../img/common/m_pivot_logo_1_k.png) no-repeat center center;
    background-size: 100%;
  }
}

.c-gnb__btn-menu {
  z-index: 6;
  position: relative;
  top: -100px;
  width: 26px;
  height: 20px;
  -webkit-tap-highlight-color: transparent;
}

@media (max-width: 751px) {
  .c-gnb__btn-menu {
    width: 25px;
    height: 21px;
  }
}

.c-gnb__btn-menu:focus {
  outline: none;
}

.c-gnb__btn-menu:not(.c-gnb__btn-menu__btn-menu--open):hover .c-gnb__btn-menu-element:nth-child(1) {
  margin-top: 8px;
}

@media (max-width: 751px) {
  .c-gnb__btn-menu:not(.c-gnb__btn-menu__btn-menu--open):hover .c-gnb__btn-menu-element:nth-child(1) {
    margin-top: 9px;
